import React, { useState } from "react";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import { saveInLocalStroage } from "../api/\btoDoApi";
import { deleteArrayFromToDos } from "../localStorage";
import { choice, toDos } from "../store";
import Button from "./common/Button";

const Wrapper = styled.div`
  max-width: 660px;
  height: 20vh;
  border: 1px solid black;
  display: flex;
  align-items: center;
  padding: 10px;
  margin: 10px;
`;

export const InputBox = styled.form`
  padding: 10px;
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  position: relative;
`;

export const InputDate = styled.input`
  font-size: 12px;
  height: 30%;
  padding: 10px;
  background-color: inherit;
  border: 1px solid;
  border-radius: 5px;
`;

export const InputContents = styled.textarea`
  width: 100%;
  height: 80%;
  padding: 10px;
  background-color: inherit;
  border: 1px solid;
  border-radius: 5px;
  resize: none;
  &::placeholder {
    color: ${(props) => props.theme.light.font};
    opacity: 1;
  }
`;

export const ButtonBox = styled.div`
  width: 30%;
  height: 100%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

function InputSection() {
  const [date, setDate] = useState("");
  const [content, setContent] = useState("");
  const [localTodos, setLocalToDos] = useRecoilState(toDos);
  const [choiceArray, setChoiceArray] = useRecoilState(choice);
  const changeDate = (event: React.FormEvent<HTMLInputElement>) => {
    setDate(event.currentTarget.value);
  };
  const changeContent = (event: React.FormEvent<HTMLTextAreaElement>) => {
    setContent(event.currentTarget.value);
  };
  const makeNewToDo = () => {
    const newToDo = {
      id: Date.now(),
      title: content,
      deadline: date,
      done: false,
    };
    saveInLocalStroage(newToDo);
    setLocalToDos(() => {
      return [...localTodos, newToDo];
    });
    setDate("");
    setContent("");
  };
  const mulDeleteClick = () => {
    if (window.confirm("선택한 TODO를 삭제하시겠나요?")) {
      if (choiceArray.length === 0) {
        alert("선택한 ToDo가 없습니다.");
        return;
      } else {
        const newList = deleteArrayFromToDos(choiceArray);
        setLocalToDos(newList);
      }
    }
  };
  return (
    <Wrapper>
      <InputBox>
        <InputDate type="date" value={date} onChange={changeDate} />
        <InputContents
          placeholder="할 것을 입력해주시오..."
          value={content}
          onChange={changeContent}
        />
      </InputBox>
      <ButtonBox>
        <Button
          text="생성하기"
          hoverColor="rgba(0,0,255,0.5)"
          clickFcn={makeNewToDo}
        />
        <Button
          clickFcn={mulDeleteClick}
          text="선택삭제"
          hoverColor="rgba(255,0,0,0.5)"
        />
      </ButtonBox>
    </Wrapper>
  );
}

export default InputSection;
